var secp256k1 = require('secp256k1');

var crypto = require("crypto");
/**
 * Sign utf-8 message with private message
 * @param {string} message
 * @param {string} privateKey
 * @return {string} signature
 */


exports.sign = function (message, privateKey) {
  var msgHash = crypto.createHash('sha256').update(message, 'utf8').digest();
  var sigObj = secp256k1.sign(msgHash, Buffer.from(privateKey, 'hex'));
  return sigObj.signature.toString('hex');
};
/**
 * Verify Message(utf-8) with signature and publicKey.
 * @param {string} message
 * @param {string} signature
 * @param {string} publicKey
 * @return {boolean}
 */


exports.verify = function (message, signature, publicKey) {
  var msgHash = crypto.createHash('sha256').update(message, 'utf8').digest();
  return secp256k1.verify(msgHash, Buffer.from(signature, 'hex'), Buffer.from(publicKey, 'hex'));
};