var _this = this;

var ecies = require('./ecies');

var sygnaSign = require('./sign');

var _require = require('../config'),
    SYGNA_BRIDGE_CENTRAL_PUBKEY = _require.SYGNA_BRIDGE_CENTRAL_PUBKEY;
/**
 * Encrypt private info object to hex string.
 * @param {object} data priv_info in object format.
 * @param {string} publicKey recipeint public key in hex string.
 * @return {string} ECIES encoded privMsg.
 */


exports.sygnaEncodePrivateObj = function (data, publicKey) {
  var msgString = JSON.stringify(data);
  var encoded = ecies.ECIESEncode(msgString, publicKey);
  return encoded;
};
/**
 * Decode private info from recipent server.
 * @param {string} privMsg 
 * @param {string} privateKey 
 * @return {object}
 */


exports.sygnaDecodePrivateObg = function (privMsg, privateKey) {
  var decoded = ecies.ECIESDecode(privMsg, privateKey);
  return JSON.parse(decoded);
};
/**
 * @param {string} private_info
 * @param {object} transaction
 * @param {string} data_dt
 * @param {string} privateKey
 * @return {{private_info: string, transaction:{}, data_dt:string, signature:string}}
 */


exports.signTransferData = function (private_info, transaction, data_dt, privateKey) {
  if (typeof private_info != "string") throw new Error("private_info should be string, got ".concat(typeof private_info));
  if (typeof data_dt != "string") throw new Error("data_dt should be string, got ".concat(typeof data_dt));
  if (typeof privateKey != "string") throw new Error("privateKey should be string, got ".concat(typeof privateKey));
  if (typeof transaction.originator_vasp_code != "string") throw new Error("transaction.originator_vasp_code should be string, got ".concat(typeof transaction.originator_vasp_code));
  if (typeof transaction.originator_addr != "string") throw new Error("transaction.originator_addr should be string, got ".concat(typeof transaction.originator_addr));
  if (typeof transaction.beneficiary_vasp_code != "string") throw new Error("transaction.beneficiary_vasp_code should be string, got ".concat(typeof transaction.beneficiary_vasp_code));
  if (typeof transaction.beneficiary_addr != "string") throw new Error("transaction.beneficiary_addr should be string, got ".concat(typeof transaction.beneficiary_addr));
  if (typeof transaction.transaction_currency != "string") throw new Error("transaction.transaction_currency should be string, got ".concat(typeof transaction.transaction_currency));
  if (typeof transaction.amount != "number") throw new Error("transaction.amount should be number, got ".concat(typeof transaction.amount));
  var data = {
    private_info: private_info,
    transaction: transaction,
    data_dt: data_dt
  };
  return _this.signObject(data, privateKey);
};
/**
 * @param {string} callback_url
 * @param {string} privateKey
 * @return {{callback_url_string, signature: string}}
 */


exports.signCallBack = function (callback_url, privateKey) {
  if (typeof callback_url != "string") throw new Error("callback_url should be string, got ".concat(typeof callback_url));
  if (typeof privateKey != "string") throw new Error("privateKey should be string, got ".concat(typeof privateKey));
  var data = {
    callback_url: callback_url
  };
  return _this.signObject(data, privateKey);
};
/**
 * @param {string} transfer_id
 * @param {string} result REJECT or ACCEPT
 * @param {string} privateKey
 * @return {{transfer_id:string, result: string, signature: string}}
 */


exports.signResult = function (transfer_id, result, privateKey) {
  if (typeof transfer_id != "string") throw new Error("transfer_id should be string, got ".concat(typeof transfer_id));
  if (typeof result != "string") throw new Error("result should be string, got ".concat(typeof result));
  if (typeof privateKey != "string") throw new Error("privateKey should be string, got ".concat(typeof privateKey));
  var data = {
    transfer_id: transfer_id,
    result: result
  };
  return _this.signObject(data, privateKey);
};
/**
 * @param {string} transfer_id
 * @param {string} txid
 * @param {string} privateKey
 * @return {{transfer_id:string, txid:string, signature:string}}
 */


exports.signSendTxId = function (transfer_id, txid, privateKey) {
  if (typeof transfer_id != "string") throw new Error("transfer_id should be string, got ".concat(typeof transfer_id));
  if (typeof txid != "string") throw new Error("txid should be string, got ".concat(typeof txid));
  if (typeof privateKey != "string") throw new Error("privateKey should be string, got ".concat(typeof privateKey));
  var data = {
    transfer_id: transfer_id,
    txid: txid
  };
  return _this.signObject(data, privateKey);
};
/**
 * Sign Objects.
 * @param {object} obj
 * @param {string} privateKey
 * @return {object} original object adding a signature field
 */


exports.signObject = function (obj, privateKey) {
  obj.signature = "";
  var msgStr = JSON.stringify(obj);
  var signature = sygnaSign.sign(msgStr, privateKey);
  obj.signature = signature.toString('hex');
  return obj;
};
/**
 * verify obj with provided pubkey or default sygna bridge publickey
 * @param {object} obj
 * @param {string?} publicKey default to sygna bridge's publickey
 * @return {boolean}
 */


exports.verifyObject = function (obj) {
  var publicKey = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : SYGNA_BRIDGE_CENTRAL_PUBKEY;
  var clone = Object.assign({}, obj);
  var signature = clone.signature;
  clone.signature = "";
  var msgStr = JSON.stringify(clone);
  return sygnaSign.verify(msgStr, signature, publicKey);
};