var crypto = require('crypto');

var Buffer = require('buffer').Buffer;
/**
 * Sygna Bridge ECIES Encode.
 * @param {string} msg text to encode (in utf-8 plain text)
 * @param {string} publicKey recipient's uncompressed publickey in hex form
 * @return {string} hex string of encoded private msg
 */


exports.ECIESEncode = function (msg, publicKey) {
  publicKey = Buffer.isBuffer(publicKey) ? publicKey : Buffer.from(publicKey, 'hex');
  msg = Buffer.from(msg);
  var ephemeral = crypto.createECDH('secp256k1');
  ephemeral.generateKeys();
  var sharedSecret = ephemeral.computeSecret(publicKey, null);
  var hashedSecret = sha512(sharedSecret, 'hex');
  var encryptionKey = hashedSecret.slice(0, 32);
  var macKey = hashedSecret.slice(32);
  var iv = Buffer.allocUnsafe(16);
  iv.fill(0);
  var ciphertext = aes256CbcEncrypt(iv, encryptionKey, msg);
  var dataToMac = Buffer.concat([iv, ephemeral.getPublicKey(), ciphertext]);
  var mac = hmacSha1(macKey, dataToMac);
  var encData = Buffer.concat([ephemeral.getPublicKey(), mac, ciphertext]);
  return encData.toString('hex');
};
/**
 * Sygna Bridge ECIES Decode.
 * @param {string} encodedMsg whole hex string encrypted by Sygna ECIES.
 * @param {string} privateKey
 */


exports.ECIESDecode = function (encodedMsg, privateKey) {
  privateKey = Buffer.isBuffer(privateKey) ? privateKey : Buffer.from(privateKey, 'hex');
  encodedMsg = Buffer.from(encodedMsg, 'hex');
  var ephemeralPubKey = encodedMsg.slice(0, 65);
  var mac = encodedMsg.slice(65, 85);
  var ciphertext = encodedMsg.slice(85);
  var recipient = crypto.createECDH('secp256k1');
  recipient.setPrivateKey(privateKey);
  var sharedSecret = recipient.computeSecret(ephemeralPubKey, null);
  var hashedSecret = sha512(sharedSecret, 'hex');
  var encryptionKey = hashedSecret.slice(0, 32);
  var macKey = hashedSecret.slice(32);
  var iv = Buffer.alloc(16);
  iv.fill(0);
  var dataToMac = Buffer.concat([iv, ephemeralPubKey, ciphertext]);
  var realMac = hmacSha1(macKey, dataToMac);
  return bytesEqual(mac, realMac) ? aes256CbcDecrypt(iv, encryptionKey, ciphertext).toString() : false;
};

var aes256CbcEncrypt = function aes256CbcEncrypt(iv, key, plaintext) {
  var cipher = crypto.createCipheriv("aes-256-cbc", key, iv);
  var firstChunk = cipher.update(plaintext);
  var secondChunk = cipher.final();
  return Buffer.concat([firstChunk, secondChunk]);
};

var aes256CbcDecrypt = function aes256CbcDecrypt(iv, key, ciphertext) {
  var decipher = crypto.createDecipheriv("aes-256-cbc", key, iv);
  var firstChunk = decipher.update(ciphertext);
  var secondChunk = decipher.final();
  return Buffer.concat([firstChunk, secondChunk]);
};

var sha512 = function sha512(msg) {
  return crypto.createHash("sha512").update(msg).digest();
};

var hmacSha1 = function hmacSha1(key, msg) {
  return crypto.createHmac("sha1", key).update(msg).digest();
};

var bytesEqual = function bytesEqual(b1, b2) {
  if (b1.length !== b2.length) {
    return false;
  }

  for (var i = 0; i < b1.length; i++) {
    if (b1[i] ^ b2[i]) return false;
  }

  return true;
};